import API from "./BaseAPI";
import { DeleteSecured } from "../client/REST";


const uri = "favourites";
const delete_uri = "delete_favourite";
const search_uri = "search";
const multiple_ids = "favourite_ids";
const object_key = "favourite";

const BaseAPI = new API({
  uri,
  delete_uri,
  search_uri,
  multiple_ids,
  object_key,
});

export default {
  ...BaseAPI,
  remove: ({params}) => {
    debugger;
    return DeleteSecured(`/${uri}/id/${params}`);
  },
};
